import React, { FC, useEffect } from 'react';
// import { useArgs } from '@storybook/addon-controls';

import { Container } from '../gridLayout';
import { Typography } from '../typography';

import styles from './Wrapper.module.scss';

interface IWrapper {
  title: string;
  children: React.ReactNode;
  style?: any;
}

const Wrapper: FC<IWrapper> = ({ title, children, style = {} }) => {
  // const [args, updateArgs] = useArgs();

  useEffect(() => {
    // document.body.setAttribute('theme', 'light');
    document.body.dataset.theme = 'light';
  }, []);

  return (
    <div className={styles['wrapper__background']}>
      <Container>
        <div className={styles.wrapper}>
          <div className={styles['wrapper__header']}>
            <Typography className={styles['wrapper__title']}>
              {title}
            </Typography>
          </div>
          <div className={styles['wrapper__body']} style={style}>
            {children}
          </div>
        </div>
      </Container>
    </div>
  );
};

export { Wrapper };
